import * as jquery from 'jquery'
(window as any).jQuery = jquery;
(window as any).$ = jquery
import './vendor/bootstrap'
import './vendor/jquery.cookie'

$(function() {
  const company = $.cookie('company')
  if (company) {
    $('.company-prefill').text(company).parent().show()
    $('.company-username').val(company).hide()

    $('.btn-change-company').click(function() {
      $('.company-prefill').parent().hide()
      $('.company-username').show().val('')
    })
  }

  $('.login-tabs a').click(function(e: JQuery.Event<HTMLElement, null>) {
    e.preventDefault()
    $(this).tab('show')
  })

  if (location.hash === '#login2') {
    $('.login-tabs a[href="#login2"]').tab('show')
  }
})



